.lightbox {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    pointer-events: none;
    transition: opacity var(--animation-duration) var(--timing-function);
  }
  
  .lightbox.is-open {
    opacity: 1;
    pointer-events: initial;
  }
  
  .lightbox__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
  }
  
  .lightbox__content {
    transform: scale(0.95);
    transition: transform var(--animation-duration) var(--timing-function);
  }
  
  .lightbox.is-open .lightbox__content {
    transform: scale(1);
  }
  
  .lightbox__image {
    display: block;
    width: auto;
    height: auto;
    max-height: 100vh;
    max-width: 100vw;
  }